import Home from '@/views/baseViews/Home'
export default [{
  path: '/',
  redirect: Home,
},
{
  path: '/Home',
  name: 'Home',
  component: () => import('@/views/baseViews/Home'),
  meta: {                          //
    showTop: true,
    showFotter: true,
    //404
    showNotFound: true,
  },
},
{
  path: '/loginView',
  name: 'loginView',
  component: () => import('@/views/baseViews/loginView'),
  meta: {
    showTop: false,
    showFotter: false,
    //404
    showNotFound: true,
  },
},
{
  path: '/registerView',
  name: 'registerView',
  component: () => import('@/views/baseViews/registerView'),
  meta: {
    showTop: true,
    showFotter: true,
    //404
    showNotFound: true,
  },
},
// 
{
  path: '/retrievePassword',
  name: 'retrievePassword',
  component: () => import('@/views/baseViews/retrievePassword'),
  meta: {
    showTop: true,
    showFotter: true,
    //404
    showNotFound: true,
  },
},
//404
{
  path: '*',
  name: 'notFound',
  component: () => import('@/views/baseViews/notFound.vue'),
  meta: {
    showTop: false,
    // showFotter:,
    //404
    showNotFound: false,
  },
}
]
