//
import {getUserInfo,changeUserInfo} from '../service/userServers'
const userInfo = {
    namespaced:true,   //
    state:{
        userInfo:{},
    },
    actions:{
      //
      getUserInfo(context){
        getUserInfo().then((res)=>{
          if(res.data.code === 200){
            let info = res.data.data;
            context.commit('GETUSERINFO',info);
          }
        })
      },
      changeUserInfo(context,data){
        changeUserInfo(data).then((res)=>{
          if(res.data.code === 200){
            let info = res.data.data;
            context.commit('GETUSERINFO',info);
          }
        })
      }
    },
    mutations:{
      GETUSERINFO(state,info){
        state.userInfo = info
      },
      CHANGEUSERINFO(state,info){
        state.userInfo = info
      }

    },
    getters:{
      //
      getUserImg(state){
        //
        let userImg = {}
        userImg.nickname = state.userInfo.nickname || ""
        userImg.image = state.userInfo.image || ""
        userImg.message = state.userInfo.message || ""
        return userImg
      },

      //
      getUserInfo(state){
        return state.userInfo
      }
    }
}
export default userInfo
