//
export default [
  {
    path: '/questionPage:questionId?',
    name: 'questionPage',
    component: () =>import('@/views/questionViews/questionPage'),
    meta: {                          
      showTop: true,
      showFotter:true,
      //404
      showNotFound:true,
    },
  },
  {
    path: '/questionSelect',
    name: 'questionSelect',
    component:  () => import('@/views/questionViews/questionSelect') ,
    meta: {                          
      showTop: true,
      showFotter:true,
      //404
      showNotFound:true,
    },
  },
  {
    path: '/questionVolume',
    name: 'questionVolume',
    component:()=>import('@/views/questionViews/questionVolume'),  
    meta: {                          
      showTop: true,
      showFotter:true,
      //404
      showNotFound:true,
    },
  },
]