export default [
  {
    path: '/teacherSelect',
    name: 'teacherSelect',
    component: () => import('@/views/teacherView/teacherSelect'),
    meta: {                          //                       
      showTop: true,
      showFotter: true,
      //404
      showNotFound: true,
    },
  },
  {
    path: '/teacherPerson/:teacherId?',
    name: 'teacherPerson',
    component: () => import('@/views/teacherView/teacherPerson'),
    meta: {
      showTop: true,
      showFotter: true,
      //404
      showNotFound: true,
    },
  }
]
