// PDF
import html2canvas from 'html2canvas'
import JsPDF from 'jspdf'
export default {
  install(Vue,) {
    Vue.prototype.printOutPDF = function () {
      let title = 'PDF'
      let shareContent = document.getElementById('main-content'),//DOM 
        width = shareContent.clientWidth, //dom 
        height = shareContent.clientHeight, //dom 
        canvas = document.createElement("canvas"), //canvas
        scale = 1; // 
      canvas.width = width * scale; //canvas  * 
      canvas.height = height * scale; //canvas *
      canvas.style.width = shareContent.clientWidth * scale + "px";
      canvas.style.height = shareContent.clientHeight * scale + "px";
      canvas.getContext("2d").scale(scale, scale); //context,scale
      let opts = {
        scale: scale, // scale 
        canvas: canvas, // canvas
        logging: false, //html2canvas
        width: width, //dom 
        height: height,
        useCORS: true, // 
      }

      html2canvas(shareContent, opts).then(() => {
        let contentWidth = canvas.width;
        let contentHeight = canvas.height;
        //pdfhtmlcanvas;
        let pageHeight = (contentWidth / 592.28) * 841.89;
        //pdfhtml
        let leftHeight = contentHeight;
        //
        let position = 30;
        //a4[595.28,841.89]htmlcanvaspdf
        let imgWidth = 595.28;
        let imgHeight = (592.28 / contentWidth) * contentHeight;
        let pageData = canvas.toDataURL("image/jpeg", 1.0);
        let PDF = new JsPDF("", "pt", "a4");
        if (leftHeight < pageHeight) {
          PDF.addImage(pageData, "JPEG", 0, 0, imgWidth, imgHeight);
        } else {
          while (leftHeight > 0) {
            PDF.addImage(pageData, "JPEG", 0, position, imgWidth, imgHeight);
            leftHeight -= pageHeight;
            position -= 841.89;
            if (leftHeight > 0) {
              PDF.addPage();
            }
          }
        }
        PDF.save(title + ".pdf"); // 
      });
    }
  }
}